<template>
  <div>
    <div class="login_wrapper">
      <div class="animate form login_form">
        <section class="login_content">
          <form @submit.prevent="handleSubmit">
            <h1>Login</h1>
            <div>
              <input
                type="text"
                v-model="users.Username"
                class="form-control"
                placeholder="Username"
                required
              />
            </div>
            <div>
              <input
                type="password"
                v-model="users.Password"
                class="form-control"
                placeholder="Password"
                required
              />
            </div>
            <div>
              <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
              <a class="reset_pass" href="#">Lost your password?</a>
            </div>
            <div class="clearfix"></div>
            <div class="separator">
              <p class="change_link">
                <img
                  v-show="submitted"
                  src="data:image/gif;base64,"
                />
              </p>
              <div class="clearfix"></div>
            </div>
          </form>
        </section>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
//import instance from '../helper/instance'
import {
  authHeader,
  responseCheck,
  AuthURL,
  notifyHelper
} from '../helper'

export default {
  data() {
    return {
      users: {
        Username: '',
        Password: ''
      },
      submitted: false
    }
  },
  created() {
    // reset login status
    this.$store.commit('removetoken')
  },
  methods: {
    handleSubmit(e) {
      this.submitted = true
      axios
        .post(AuthURL + 'authenticate', {
          username: this.users.Username,
          password: this.users.Password
        })
        .then(response => {
          if (response.data.error) {
            if(response.data.locked){
              notifyHelper('error', 'Your account is locked please contact SDU,D1!')
            }
            else
            {
              notifyHelper('error', response.data.message)
            }
          } 
          else 
          {
            this.$store.commit('addtoken', response.data.token)
            this.$store.commit('addrole', response.data.role)
            if (!response.data.tfa) {
              this.$store.commit('haspsk', false)
              this.$store.commit('addpsk', response.data.key)
              this.$store.commit('adduri', response.data.psk)
              this.$router.replace({ name: 'addtfa' })
            } else {
              this.$store.commit('addtfa', true)
              this.$router.replace({ name: 'tfa' })
            }
          }
        })
        // .catch(error => {
        //   //console.log(error.response.data.message)
        //   this.submitted = false
        //   responseCheck(error.response.status)
        // })
        // .finally()
    }
  }
}
</script>



