<template>
  <div>
    <div class="col-md-12" style="overflow: auto">
      <form class="form-inline" @submit.prevent="searchData()" role="form">
        <div class="col-md-2">
          <p class="input-group">
            <input
              type="text"
              class="form-control"
              v-model="pagingInfo.search"
              placeholder="Register No."
            />
          </p>
        </div>

        <div class="col-md-3">
          <p class="input-group">
            <button type="submit" class="btn btn-info btn-sm">
              <strong>Search</strong>
            </button>
          </p>
        </div>
        <div class="col-md-2"></div>
      </form>
      <div class="clearfix"></div>
      <b-pagination
        v-model="pagingInfo.page"
        v-on:change="fetchData()"
        :total-rows="totalitems"
        :per-page="pagingInfo.itemsPerPage"
        first-text="First"
        prev-text="Prev"
        next-text="Next"
        last-text="Last"
      ></b-pagination>
      <table class="table table-striped table-bordered table-hover">
        <thead>
          <tr>
            <th>Type</th>
            <th>Register No</th>
            <th>Country</th>
            <th>State</th>
            <th>City</th>
            <th>Postal Code</th>
            <th>Comments</th>
            <th>Created By</th>
            <th>
              <a @click="sort('DateCreated')">Date Created</a>
            </th>
            <th>Updated By</th>
            <th>
              <a @click="sort('DateUpdated')">Date Updated</a>
            </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="doc in docs" v-bind:key="doc.id">
            <td>{{doc.type | capitalize}}</td>
            <td>{{doc.registerNo}}</td>
            <td>{{doc.country}}</td>
            <td>{{doc.state}}</td>
            <td>{{doc.city}}</td>
            <td>{{doc.postalCode}}</td>
            <td>{{doc.comments}}</td>
            <td>{{doc.createdBy}}</td>
            <td>{{doc.dateCreated | formatDate}}</td>
            <td>{{doc.updatedBy}}</td>
            <td>{{doc.dateUpdated | formatDate}}</td>
            <td>
              <a @click="More(doc)" title="View Linkages" class="btn btn-sm btn-default">
                <i class="glyphicon glyphicon-resize-small"></i>
              </a>
            </td>
          </tr>
        </tbody>
      </table>
      <div v-show="totalitems == 0" class="alert alert-warning">
        <p>Found no result try again</p>
      </div>
      <b-pagination
        v-model="pagingInfo.page"
        v-on:change="fetchData()"
        :total-rows="totalitems"
        :per-page="pagingInfo.itemsPerPage"
        first-text="First"
        prev-text="Prev"
        next-text="Next"
        last-text="Last"
      ></b-pagination>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { authHeader, responseCheck, RecordURL, notifyHelper } from '../helper'
export default {
  data() {
    return {
      docs: [],
      pagingInfo: {
        page: 1,
        itemsPerPage: 20,
        search: '',
        sortBy: '',
        reverse: false
      },
      totalitems: null
    }
  },
  methods: {
    fetchData() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getrealestate', {
          headers: authHeader(),
          params: this.pagingInfo
        })
        .then(response => {
          this.docs = response.data.data
          this.totalitems = response.data.count
          this.$Progress.finish()
        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchData() {
      this.pagingInfo.page = 1
      this.fetchData()
    },
    More: function(data) {
      var additional1 = data.type
      additional1 = additional1.replace(/\//g, '-')
      this.$router.replace({
        name: 'Links',
        params: { id: data.id, description: additional1 }
      })
    },
    sort: function(sortBy) {
      if (sortBy === this.pagingInfo.sortBy) {
        this.pagingInfo.reverse = !this.pagingInfo.reverse
      } else {
        this.pagingInfo.sortBy = sortBy
        this.pagingInfo.reverse = false
      }
      this.pagingInfo.page = 1
      this.fetchData()
    }
  }
}
</script>




