import Vue from 'vue'
import Router from 'vue-router'
//import Builder from '@/components/LinkBuilder'
import Persons from '@/components/Person'
//import Share from '@/components/Share'
import Organizations from '@/components/Organization'
import Events from '@/components/Event'
import financial from '@/components/FinalcialResources.vue'
import firearms from '@/components/FireArms.vue'
import legal from '@/components/LegalCharges.vue'
import paper from '@/components/Papers.vue'
import realestate from '@/components/RealEstate.vue'
import telephone from '@/components/Telephone.vue'
import transpotation from '@/components/Transportation.vue'
import Encodes from '@/components/MyEncodes'
import Documents from '@/components/Document'
import RegionalData from '@/components/RegionalData'
import Login from '@/components/Login'
import tfa from '@/components/2fa'
import addtfa from '@/components/addtfa'
import report from '@/components/Report.vue'
import links from '@/components/Links.vue'
import loginmaster from '@/components/master/loginmaster'
import master from '@/components/master/master'
import { store } from '../store/store'

Vue.use(Router)

export const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: loginmaster,
      children : [
        {
          path: '/login',
          name: 'login',
          component: Login
        },
        {
          path: '/tfa',
          name: 'tfa',
          component: tfa
        },
        {
          path : '/addtfa',
          name : 'addtfa',
          component : addtfa
        }
        
      ]
    },
    {
    path: "/",
    component: master,
    children: [
      {
        path: '/Persons',
        name: 'Persons',
        component: Persons
      },
      {
        path: '/Dash',
        name: 'Dash',
        component: report
      },
      {
        path: '/Documents',
        name: 'Documents',
        component: Documents
      },
      {
        path: '/Organizations',
        name: 'Organization',
        component: Organizations
      },
      {
        path: '/Events',
        name: 'Events',
        component: Events
      },
      {
        path: '/Financial',
        name: 'Financial',
        component: financial
      },
      {
        path: '/FireArms',
        name: 'FireArms',
        component: firearms
      },
      {
        path: '/LegalCharges',
        name: 'LegalCharges',
        component: legal
      },
      {
        path: '/OffcialPapers',
        name: 'OfficialPapers',
        component: paper
      },
      {
        path: '/RealEstate',
        name: 'RealEstate',
        component: realestate
      },
      {
        path: '/Telephone',
        name: 'Telephone',
        component: telephone
      },
      {
        path: '/Transportation',
        name: 'Transportation',
        component: transpotation
      },
      {
        path: '/Encodes',
        name: 'Encodes',
        component: Encodes
      },
      {
        path: '/RegionalData',
        name: 'RegionalData',
        component: RegionalData
      },
      {
        path: '/Links/:id/:description',
        name: 'Links',
        component: links
      }
    ]
    }
    
    
    // },
    // {
    //   path : '/Build/:id/:type',
    //   name : 'Build',
    //   component : Builder
    // },
    // {
    //   path : '/Sample',
    //   name : 'Sample',
    //   component : Sample
    // }
  ]
})

router.beforeEach((to, from, next) => {
  // redirect to login page if not logged in and trying to access a restricted page
  const publicPages = ['/login'];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = store.getters.token;

  if (authRequired && !loggedIn) {
    return next('/login');
  }
 
  next();
})






