<template>
  <div class="col-md-12" style="overflow: auto">
    <br />
    <form class="form-inline" @submit.prevent="searchData()" role="form">
      <div class="form-group">
        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.search"
          placeholder="First Name..."
        />
        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.mname"
          placeholder="Middle Name..."
        />
        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.lname"
          placeholder="Last Name..."
        />
        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.alias"
          placeholder="Alias..."
        />
        <button type="submit" class="btn btn-info btn-sm">
          <strong>Search</strong>
        </button>
      </div>
    </form>
    <br />
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
    <table class="table table-striped table-bordered table-hover">
      <thead>
        <tr>
          <th>
            <a @click="sort('FirstName')">First Name</a>
          </th>
          <th>
            <a @click="sort('LastName')">Last Name</a>
          </th>
          <th>
            <a @click="sort('MiddleName')">Middle Name</a>
          </th>
          <th>
            <a @click="sort('Alias')">Alias</a>
          </th>
          <th>Comments</th>
          <th>Birth Date</th>
          <th>Nationality</th>
          <th>Created By</th>
          <th>
            <a @click="sort('DateCreated')">Date Created</a>
          </th>
          <th>Updated By</th>
          <th>
            <a @click="sort('DateUpdated')">Date Updated</a>
          </th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in persons" :key="user.id">
          <!--<td><p ng-hide="doc.n4sh == 'Y'">{{user.id}}</p></td>-->
          <td>
            <a @click="More(user)">{{user.firstName | capitalize}}</a>
          </td>
          <td>{{user.lastName | capitalize}}</td>
          <td>{{user.middleName | capitalize}}</td>
          <td>{{user.alias | capitalize}}</td>
          <td>{{user.comments | capitalize}}</td>
          <td>{{user.birthDate | formatDate}}</td>
          <td>{{user.nationality}}</td>
          <td>{{user.createdBy}}</td>
          <td>{{user.dateCreated | formatDate}}</td>
          <td>{{user.updatedBy}}</td>
          <td>{{user.dateUpdated | formatDate}}</td>
          <td>
            <a
              @click="soidb(user.id)"
              target="_blank"
              class="btn btn-sm btn-default"
              title="View SOI from database"
            >
              <i class="glyphicon glyphicon-book"></i>
            </a>&nbsp;
            <a
              @click="sois(user.id)"
              class="btn btn-sm btn-default"
              title="View Downloadable Profile"
            >
              <i class="glyphicon glyphicon-download-alt"></i>
            </a>&nbsp;
            <a @click="More(user)" title="View Linkages" class="btn btn-sm btn-default">
              <i class="glyphicon glyphicon-resize-small"></i>
            </a>

            &nbsp;
          </td>
        </tr>
      </tbody>
      <tfoot></tfoot>
    </table>

    <div v-show="totalitems == 0" class="alert alert-warning">
      <p>Found no result try again</p>
    </div>
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
    <!-- Modal For Uploaded SOI -->
    <div>
      <b-modal id="soilist" title="Uploaded SOI" scrollable hide-footer>
        <table class="table table-bordered table-responsive">
          <thead>
            <tr>
              <th>File Name</th>
              <th>Date</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in soi" :key="item.id">
              <td>{{item.fileName}}</td>
              <td>{{item.lastModifiedDate | soiDate}}</td>
              <td>
                <a
                  @click="downloadsoi(item.id,item.fileName)"
                  class="btn btn-sm btn-success"
                  title="Downloadable SOI"
                >
                  <i class="glyphicon glyphicon-download-alt"></i>
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </b-modal>
    </div>
    <!-- Modal For SOI from DB -->
    <div>
      <b-modal id="soifromdb" title="FullText SOI" scrollable hide-footer size="xl">
        <span v-html="soifromdb"></span>
      </b-modal>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { authHeader, responseCheck, RecordURL, notifyHelper } from '../helper'
export default {
  data() {
    return {
      soifromdb: '',
      persons: [],
      pagingInfo: {
        page: 1,
        itemsPerPage: 20,
        search: '',
        mname: '',
        lname: '',
        sortBy: ''
      },
      totalitems: null,
      soi: []
    }
  },
  methods: {
    fetchData() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlist', {
          headers: authHeader(),
          params: this.pagingInfo
        })
        .then(response => {
          this.persons = response.data.data
          this.totalitems = response.data.count
          this.$Progress.finish()
        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchData() {
      this.pagingInfo.page = 1
      this.fetchData()
    },
    More: function(data) {
      var additional1 =
        data.firstName + data.middleName + data.lastName + data.alias
      additional1 = additional1.replace(/\//g, '-')
      this.$router.replace({
        name: 'Links',
        params: { id: data.id, description: additional1 }
      })
    },
    gotoLink: function(data) {
      var label = data.title == null ? 'No Title' : data.title
      ;($window.location.href =
        '/Encoder/Link?id=' + data.id + '&t=DC' + '&l=' + label),
        '_blank'
      return
    },
    sort: function(sortBy) {
      if (sortBy === this.pagingInfo.sortBy) {
        this.pagingInfo.reverse = !this.pagingInfo.reverse
      } else {
        this.pagingInfo.sortBy = sortBy
        this.pagingInfo.reverse = false
      }
      this.pagingInfo.page = 1
      this.fetchData()
    },
    sois: function(id) {
      this.$Progress.start()
      axios
        .get(RecordURL + 'soi?pID=' + id, {
          headers: authHeader()
        })
        .then(response => {
          if (response.data.withsoi) {
            //modal
            this.soi = response.data.data
            //fireup modal
            this.showModal()
          } else {
            notifyHelper('warn', 'SOI files not found')
          }
        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    downloadsoi: function(folder, file) {
      window.open(
        RecordURL + 'downloadsoi?folder=' + folder + '&filename=' + file,
        '_blank',
        ''
      )
    },
    showModal: function() {
      this.$bvModal.show('soilist')
    },
    soidb: function(id) {
      this.$Progress.start()
      axios
        .get(RecordURL + 'soidb?id=' + id + '&type=PN', {
          headers: authHeader()
        })
        .then(response => {
          if (response.data.withsoi) {
            this.soifromdb = response.data.data
            this.$bvModal.show('soifromdb')
          } else {
            notifyHelper('warn', 'SOI from database not found')
          }
        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    }
  }
}
</script>


