<template>
  <div>
    <div class="login_wrapper">
      <section class="login_content">
        <form @submit.prevent="handleSubmit" class="form-signin">
          <h1>2FA</h1>
          <div>
            <input
              type="text"
              :class="{ 'is-invalid': submitted && !users.code }"
              v-model="users.code"
              class="form-control"
              placeholder="6 Digit Code"
              autofocus
            />
            <div v-show="submitted && !users.tfa" class="invalid-feedback">6 Digit Code is required</div>
          </div>

          <div>
            <button class="btn btn-lg btn-primary btn-block" type="submit">Submit Code</button>
          </div>

          <img
            v-show="submitted"
            src="data:image/gif;base64,"
          />
        </form>
      </section>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import { instance, authHeader, responseCheck, AuthURL, notifyHelper } from '../helper'

export default {
  data() {
    return {
      users: {
        code: ''
      },
      paramsinfo: {
        code: ''
      },
      submitted: false
    }
  },
  methods: {
    handleSubmit(e) {
      this.submitted = true
      this.paramsinfo.code = this.users.code
      axios
        .post(AuthURL + 'tfa', this.paramsinfo, {
          headers: authHeader()
        })
        .then(response => {
          if (response.data.authorized) {
            this.$store.commit('haspsk', true)
            this.$store.commit('addtfa', true)
            this.$router.replace({ name: 'Dash' })
          } else {
            this.submitted = false
            // this.$notify({
            //   group: 'info',
            //   type: 'error',
            //   text: 'Invalid Two Factor Codes!'
            // })
            notifyHelper('error', 'Invalid Two Factor Codes!')
            // this.$toast.error("Invalid Two Factor Codes!", {
            //   position: "top-center",
            //   timeout: 5000,
            //   closeOnClick: true,
            //   pauseOnFocusLoss: true,
            //   pauseOnHover: true,
            //   draggable: true,
            //   draggablePercent: 0.6,
            //   showCloseButtonOnHover: false,
            //   hideProgressBar: true,
            //   closeButton: "button",
            //   icon: true,
            //   rtl: false
            // });
          }
        })
        .catch(error => {
          if (error.response.status === 401) {
            this.$notify({
              group: 'info',
              type: 'error',
              text: 'Session has already expired please re login!'
            })
            this.$router.replace({ name: 'login' })
          }
        })
        .finally()
    }
  }
}
</script>


