<template>
    <div>
         <div class="login_wrapper">
            <section class="login_content">.
                <form @submit.prevent="handleSubmit">
           
            <h1>Scan and Verify!</h1>
            
            <div>
                <qrcode-vue :value="users.psk" :size="300" level="H"></qrcode-vue>
            </div>
            <div>
                <input type="text" v-model="users.tfa" id="inputEmail" class="form-control" :class="{ 'is-invalid': submitted && !users.tfa }" placeholder="6 Digit Code"  autofocus>
                <div v-show="submitted && !users.tfa" class="invalid-feedback">6 Digit Code is required</div>
            </div>
            <div>
                <button class="btn btn-lg btn-primary btn-block" type="submit">Verify</button>
            </div>
            
            
            
            <img v-show="submitted" src="data:image/gif;base64," />
            
            
        </form>
            </section>
         </div>
        
    </div>
</template>
<script>
import axios from 'axios'
import {instance, authHeader,responseCheck, AuthURL } from '../helper';
import QrcodeVue from 'qrcode.vue'

export default {
    data () {
        return {
            users:{
                psk: this.$store.getters.uri,
                tfa : ''
            },
            submitted: false,
            paramsinfo:{
                key : '',
                code : ''
            }
            
        }
    },
    components: {
      QrcodeVue,
    },
    methods: {
        handleSubmit (e) {
            this.submitted = true;
            this.paramsinfo.code = this.users.tfa
            this.paramsinfo.key = this.$store.getters.psk

            axios.post(
                AuthURL + 'addtfa', this.paramsinfo,
                {   
                        headers : authHeader()
                        
                }
                )
                .then(response => {
                    if(response.data.authorized){
                       
                        this.$store.commit('haspsk', true);
                        this.$store.commit('addtfa', true);
                         this.$router.replace({name: 'Person'})
                    }else{
                        this.$notify({ group: 'info', type: 'error', text: 'Invalid Two Factor Codes!' })
                        this.submitted = false;
                    }
                })
                .catch(error => {
                    if(error.response.status === 401){
                        this.submitted = true;
                    this.$notify({ group: 'info', type: 'error', text: 'Session has already expired please re login!' })
                    this.$router.replace({name: 'login'})
                    
                    }
                })
                .finally()
    
        }
    }
};
</script>


