import Vue from 'vue'
import {router} from '../router'
export function responseCheck(status) {
    if (status === 401)
    {
          Vue.$toast.error('Session has already expired please re login!', {
            position: "top-center",
            timeout: 5000,
            closeOnClick: true,
            pauseOnFocusLoss: false,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: false,
            closeButton: "button",
            icon: true,
            rtl: false
          });
          router.replace("/login")
    }
    else if(status === 403)
    {
        
          Vue.$toast.error('You are not allowed to access this resource!', {
            position: "top-center",
            timeout: 5000,
            closeOnClick: true,
            pauseOnFocusLoss: false,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: false,
            closeButton: "button",
            icon: true,
            rtl: false
          });
    }
    else
    {
        // Vue.notify({
        //     group: 'info',
        //     type: 'error',
        //     text: 'There was an error processingyour request please try again later or report the matter to SDU,D1!'
        //   })

          Vue.$toast.error('There was an error processing your request please try again later or report the matter to SDU,D1!', {
            position: "top-center",
            timeout: 5000,
            closeOnClick: true,
            pauseOnFocusLoss: false,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: false,
            closeButton: "button",
            icon: true,
            rtl: false
          });
    }
}


