<template>
  <div class="col-md-12">
    <br>
    <form class="form-inline" v-on:submit="searchData()" role="form">
      <div class="form-group">
        
          <input
            type="date"
            class="form-control input-sm"
            v-model="pagingInfo.datestart"
            placeholder="Date Start"
          >

          <input
            type="date"
            class="form-control input-sm"
            v-model="pagingInfo.dateend"
            placeholder="Date End"
          >
          <input
            type="text"
            class="form-control input-sm"
            v-model="pagingInfo.search"
            placeholder="Subject/Title"
          >
     
          <input
            type="text"
            class="form-control input-sm"
            v-model="pagingInfo.reportnumber"
            placeholder="Report Number"
          >
          
          <button type="submit" class="btn btn-info btn-sm">
            <strong>Search</strong>
          </button>
      
      </div>
      
    </form>
    <br>
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
    <table class="table table-striped table-bordered table-hover">
      <thead>
        <tr>
          <!--<th>ID</th>-->
          <th>
            <a @click="sort('SubjectTitle')">Subject</a>
          </th>
          <th>
            <a @click="sort('ReportNo')">Report No.</a>
          </th>
          <th>
            <a @click="sort('ReportDate')">Report Date</a>
          </th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="doc in documents" :key="doc.id">
          <!--<td><p ng-hide="doc.n4sh == 'Y'">{{doc.id}}</td>-->
          <td>
            <a
              v-bind:href="`/Download/ViewDocs?id=${doc.id}&t=DC`"
              target="_blank" title="View Document"
            >{{doc.title | capitalize }}</a>
          </td>
          <td>
            <p>{{doc.reportNo}}</p>
          </td>
          <td>{{doc.dateEncode | formatDate}}</td>
          <td>
            <a
              v-bind:href="`/Encoder/Edit/Document?Id=${doc.id}`"
              target="_blank"
              class="btn btn-sm btn-default"
              title="Edit"
            >
              <i class="glyphicon glyphicon-pencil"></i>
            </a>
            &nbsp;
            <a
              v-bind:href="`/Download/ViewDocs?id=${doc.id}&t=DC`"
              target="_blank"
              class="btn btn-sm btn-default"
              title="View/Download File"
            >
              <i class="glyphicon glyphicon-download-alt"></i>
            </a>
            &nbsp;
            <a
              v-bind:href="`/Download/FullText?pid=${doc.id}&dtype=DC`"
              target="_blank"
              class="btn btn-sm btn-default"
              title="View Full Text"
            >
              <i class="glyphicon glyphicon-file"></i>
            </a>
            &nbsp;
            <a @click="More(doc)" title="View Linkages" class="btn btn-sm btn-default">
              <i class="glyphicon glyphicon-resize-small"></i>
            </a>
            <!-- &nbsp;
            <a @click="gotoLink(doc)" class="btn btn-sm btn-default" title="Link Diagram">
              <i class="glyphicon glyphicon-resize-full"></i>
            </a> -->
            &nbsp;
            <a v-bind:href="`/Encoder/Builder?i=${doc.id}&b=DC`" class="btn btn-sm btn-default" title="Link Builder">
              <i class="glyphicon glyphicon-random"></i>
            </a>
          </td>
        </tr>
      </tbody>
      <tfoot></tfoot>
    </table>
    <div v-show="totalitems == 0" class="alert alert-warning">
      <p>Found no result try again</p>
    </div>
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      documents: [],
      pagingInfo: {
        page: 1,
        itemsPerPage: 20,
        sortBy: 'SubjectTitle',
        reverse: false,
        search: '',
        reportnumber: '',
        datestart: '',
        dateend: ''
      },
      totalitems: null
    }
  },
  methods: {
    fetchData() {
      this.$Progress.start()
      axios
        .get(
          '/api/RECORD/getdocregiondata',
          { params: this.pagingInfo }
        )
        .then(response => {
          this.documents = response.data.data
          this.totalitems = response.data.count
        })
        .catch(error => {
          
        })
        .finally(() => this.$Progress.finish())
    },
    searchData() {
      this.pagingInfo.page = 1
      this.fetchData()
    },
    More: function(data) {
      var additional = data.title
      additional = additional.replace(/\//g, '-')
      this.$router.push({
        name: 'Links',
        params: { id: data.id, description: additional }
      })
    },
    gotoLink: function(data) {
      var label = data.title == null ? 'No Title' : data.title;
      window.location.href =
        '/Encoder/Link?id=' + data.id + '&t=DC' + '&l=' + label,
        '_blank'
      return
    },
    buildLink: function(data, type) {
      var label = data.title == null ? 'No Title' : data.title
      this.$router.push({
        name: 'Build',
        params: { id: data.id, type: type }
      })
    },
    sort: function(sortBy) {
      if (sortBy === this.pagingInfo.sortBy) {
        this.pagingInfo.reverse = !this.pagingInfo.reverse
      } else {
        this.pagingInfo.sortBy = sortBy
        this.pagingInfo.reverse = false
      }
      this.pagingInfo.page = 1
      this.fetchData()
    }
  }
}
</script>



