<template>
  <div class="col-md-12" style="overflow : auto">
    <br />
    <form class="form-inline" @submit.prevent="searchData()" role="form">
      <div class="form-group">
        <input
          type="date"
          class="form-control input-sm"
          v-model="pagingInfo.datestart"
          placeholder="Date Start"
        />

        <input
          type="date"
          class="form-control input-sm"
          v-model="pagingInfo.dateend"
          placeholder="Date End"
        />
        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.search"
          placeholder="Subject/Title"
        />

        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.reportnumber"
          placeholder="Report Number"
        />

        <select id="region" class="form-control input-sm" v-model="pagingInfo.region">
          <option value>Select Region</option>
          <option value="D010">NCR</option>
          <option value="D011">RO1</option>
          <option value="D012">RO2</option>
          <option value="D013">RO3</option>
          <option value="D014">RO4</option>
          <option value="D015">RO5</option>
          <option value="D016">RO6</option>
          <option value="D017">RO7</option>
          <option value="D018">RO8</option>
          <option value="D019">RO9</option>
          <option value="D020">RO10</option>
          <option value="D021">RO11</option>
          <option value="D022">RO12</option>
        </select>

        <button type="submit" class="btn btn-info btn-sm">
          <strong>Search</strong>
        </button>
      </div>
    </form>
    <br />
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
    <table class="table table-striped table-bordered table-hover">
      <thead>
        <tr>
          <!--<th>ID</th>-->
          <th>
            <a @click="sort('SubjectTitle')">Subject</a>
          </th>
          <th>
            <a @click="sort('ReportNo')">Report No.</a>
          </th>
          <th>
            <a @click="sort('ReportDate')">Report Date</a>
          </th>
          <th>Created By</th>
          <th>
            <a @click="sort('DateCreated')">Date Created</a>
          </th>
          <th>Updated By</th>
          <th>
            <a @click="sort('DateUpdated')">Date Updated</a>
          </th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="doc in documents" :key="doc.id">
          <!--<td><p ng-hide="doc.n4sh == 'Y'">{{doc.id}}</td>-->
          <td>
            {{doc.title | capitalize }}
          </td>
          <td>
          {{doc.reportNo}}
          </td>
          <td>{{doc.ReportDate | formatDate}}</td>
          <td>{{doc.createdBy}}</td>
          <td>{{doc.dateCreated | formatDate}}</td>
           <td>{{doc.updatedBy}}</td>
          <td>{{doc.dateUpdated | formatDate}}</td>
          <td>
            <a
              @click="getfile(doc.id)"
              target="_blank"
              class="btn btn-sm btn-default"
              title="View/Download File"
            >
              <i class="glyphicon glyphicon-download-alt"></i>
            </a>
            &nbsp;
            <a
              @click="More(doc)"
              title="View Linkages"
              class="btn btn-sm btn-default"
            >
              <i class="glyphicon glyphicon-resize-small"></i>
            </a>
            <!-- &nbsp;
            <a
              @click="gotoLink(doc)"
              class="btn btn-sm btn-default"
              title="Link Diagram"
            >
              <i class="glyphicon glyphicon-resize-full"></i>
            </a> -->
          </td>
        </tr>
      </tbody>
      <tfoot></tfoot>
    </table>
    <div v-show="totalitems == 0" class="alert alert-warning">
      <p>Found no result try again</p>
    </div>
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
  </div>
</template>

<script>
import axios from 'axios'
import {
  instance,
  authHeader,
  responseCheck,
  AuthURL,
  RecordURL,
  notifyHelper
} from '../helper'
export default {
  data() {
    return {
      documents: [],
      pagingInfo: {
        page: 1,
        itemsPerPage: 20,
        sortBy: 'SubjectTitle',
        reverse: false,
        search: '',
        reportnumber: '',
        region: '',
        datestart: '',
        dateend: ''
      },
      totalitems: null
    }
  },
  methods: {
    fetchData() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getdoc', {
          headers: authHeader(),
          params: this.pagingInfo
        })
        .then(response => {
          this.documents = response.data.data
          this.totalitems = response.data.count
        })
        .catch(error => {
           responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchData() {
      this.pagingInfo.page = 1
      this.fetchData()
    },
    More: function(data) {
      var additional = data.title
      additional = additional.replace(/\//g, '-')
      this.$router.push({
        name: 'Links',
        params: { id: data.id, description: additional }
      })
    },
    sort: function(sortBy) {
      if (sortBy === this.pagingInfo.sortBy) {
        this.pagingInfo.reverse = !this.pagingInfo.reverse
      } else {
        this.pagingInfo.sortBy = sortBy
        this.pagingInfo.reverse = false
      }
      this.pagingInfo.page = 1
      this.fetchData()
    },
    download : function(docID){
      window.open(RecordURL + 'downloadcopy?docID=' + docID,'_blank', '');

    },
    getfile: function(id){
      this.$Progress.start()
      axios
        .get(RecordURL + 'checkfiles?docID=' + id, {
          headers: authHeader()
        })
        .then(response => {
          if(response.data.withfiles)
          {
              this.download(id);
          }
          else
          {
            notifyHelper('info', 'Attached file not found or the file server was turned off!')

          }

        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    }
  }
}
</script>



