<template>
  <div class="col-md-12">
    <br>
    <form class="form-inline"  @submit.prevent="searchData()" role="form">
      <div class="form-group">
        <input
          type="text"
          class="form-control input-sm"
          v-model="pagingInfo.search"
          placeholder="Search..."
          required
        >
        <button type="submit" class="btn btn-info btn-sm">
          <strong>Search</strong>
        </button>
      </div>
    </form>
    <br>
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
    <table class="table table-striped table-bordered table-hover">
      <thead>
        <tr>
            <th>
              <a @click="sort('Type')">Type</a>
            </th>
            <th>
              <a @click="sort('Subject')">Subject</a>
            </th>
            <th>Comments</th>
            <th>Created By</th> -->
            <th><a @click="sort('DateCreated')">Date Created</a></th>
            <th>Updated By</th>
            <th><a @click="sort('DateUpdated')">Date Updated</a></th>
            <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in events" :key="user.id">
          <td>{{user.type}}</td>
          <td>{{user.title | capitalize}}</td>
          <td>{{user.comments | capitalize}}</td>
          <td>{{user.createdBy}}</td>
          <td>{{user.dateCreated | formatDate}}</td>
          <td>{{user.updatedBy}}</td>
          <td>{{user.dateUpdated | formatDate}}</td>
          <td>
            <a @click="More(user)" title="View Linkages" class="btn btn-sm btn-default">
              <i class="glyphicon glyphicon-resize-small"></i>
            </a>
            &nbsp;
            <!-- <a
              @click="gotoLink(user)"
              class="btn btn-sm btn-default"
              title="Link Diagram"
            >
              <i class="glyphicon glyphicon-resize-full"></i>
            </a> -->
          </td>
        </tr>
      </tbody>
      <tfoot></tfoot>
    </table>

    <div v-show="totalitems == 0" class="alert alert-warning">
      <p>Found no result try again</p>
    </div>
    <b-pagination
      v-model="pagingInfo.page"
      v-on:change="fetchData()"
      :total-rows="totalitems"
      :per-page="pagingInfo.itemsPerPage"
      first-text="First"
      prev-text="Prev"
      next-text="Next"
      last-text="Last"
    ></b-pagination>
  </div>
</template>

<script>
import axios from 'axios'
import {authHeader,responseCheck, RecordURL } from '../helper';
export default {
  data() {
    return {
      events: [],
      pagingInfo: {
        page: 1,
        itemsPerPage: 20,
        sortBy: 'Type',
        reverse: false,
        search: ''
      },
      totalitems: null
    }
  },
  methods: {
    fetchData() {
      this.$Progress.start()
      axios
        .get(
          RecordURL  + 'getevent',
          {
             headers : authHeader(),
             params: this.pagingInfo
          }
        )
        .then(response => {
          this.events = response.data.data
          this.totalitems = response.data.count
        })
        .catch(error => {
         responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchData() {
      this.pagingInfo.page = 1
      this.fetchData()
    },
    More: function(data) {
      var additional = data.title
      additional = additional.replace(/\//g, '-')
      this.$router.push({
        name: 'Links',
        params: { id: data.id, description: additional }
      })
    },
    sort: function(sortBy) {
      if (sortBy === this.pagingInfo.sortBy) {
        this.pagingInfo.reverse = !this.pagingInfo.reverse
      } else {
        this.pagingInfo.sortBy = sortBy
        this.pagingInfo.reverse = false
      }
      this.pagingInfo.page = 1
      this.fetchData()
    }
  }
}
</script>


