        itemsPerPage: 20,
        sortBy: 'DateStart',
        reverse: false
      },
      totalitemsrealestate: null,
      telephone: [],
      pagingInfoTelephone: {
        id: null,
        page: 1,
        itemsPerPage: 20,
        sortBy: 'DateStart',
        reverse: false
      },
      totalitemstelephone: null,
      papers: [],
      pagingInfoPapers: {
        id: null,
        page: 1,
        itemsPerPage: 20,
        sortBy: 'DateStart',
        reverse: false
      },
      totalitemspaper: null,
      transportation: [],
      pagingInfoTransportation: {
        id: null,
        page: 1,
        itemsPerPage: 20,
        sortBy: 'DateStart',
        reverse: false
      },
      totalitemstransportation: null
    }
  },
  watch: {
    '$route.params.id': function(id) {
      this.pagingInfo.id = id
      this.pagingInfoPerson.id = id
      this.pagingInfoOrganization.id = id
      this.pagingInfoEvents.id = id
      this.pagingInfoFinancial.id = id
      this.pagingInfoFirearms.id = id
      this.pagingInfoLegalCharges.id = id
      this.pagingInfoRealEstates.id = id
      this.pagingInfoTelephone.id = id
      this.pagingInfoPapers.id = id
      this.pagingInfoTransportation.id = id
    },
    '$route.params.description': function(description) {
      this.description = description
    }
  },
  created: function() {
    this.description = this.$route.params.description
    this.pagingInfo.id = this.$route.params.id
    this.pagingInfoPerson.id = this.$route.params.id
    this.pagingInfoOrganization.id = this.$route.params.id
    this.pagingInfoEvents.id = this.$route.params.id
    this.pagingInfoFinancial.id = this.$route.params.id
    this.pagingInfoFirearms.id = this.$route.params.id
    this.pagingInfoLegalCharges.id = this.$route.params.id
    this.pagingInfoRealEstates.id = this.$route.params.id
    this.pagingInfoTelephone.id = this.$route.params.id
    this.pagingInfoPapers.id = this.$route.params.id
    this.pagingInfoTransportation.id = this.$route.params.id
  },
  methods: {
    downloadSelected: function() {
      //buttondownload need to count the length of todownload show if there is an entry
      let downloads = JSON.stringify(this.selected)
      this.submitted = true
      axios
        .post(RecordURL + 'files', downloads, {
          headers: authHeaderjson()
        })
        .then(response => {
          if (response.data.withfiles) {
            this.toDownload = []
            this.selected = []
            window.open(
              RecordURL + 'downloadzip?zip=' + response.data.data,
              '_blank',
              ''
            )
          } else {
            notifyHelper(
              'info',
              'Files are not available the file server might have been turned off!'
            )
          }
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => (this.submitted = false))
    },
    clearSelected: function() {
      //button clear need to count the length of todownload show if there is an entry
      if (confirm('clear selected/added report?')) {
        this.toDownload = []
        this.selected = []

        notifyHelper('info', 'Files to download cleared!')
      }
    },
    editSelected: function() {
      //modal
      if (this.toDownload.length > 0) {
        this.$bvModal.show('selectedddocuments')
      }
    },
    addSelected: function(item) {
      for (let i = 0; i < this.toDownload.length; i++) {
        if (this.toDownload[i].ID === item.id) {
          notifyHelper('warn', 'Document Already on the list!')
          return
        }
      }
      this.toDownload.push({ ID: item.id })
      this.selected.push({
        ID: item.id,
        SUBJECT: item.subject,
        SOURCENAME: item.sourcename,
        REPORTNUMBER: item.reportnum
      })
      notifyHelper('info', 'Document Added to the download list!')
    },
    removeSelected: function(items) {
      this.toDownload = this.toDownload.filter(item => item.ID !== items.ID)
      this.selected = this.selected.filter(item => item.ID !== items.ID)
    },
    download: function(docID) {
      window.open(RecordURL + 'downloadcopy?docID=' + docID, '_blank', '')
    },
    getfile: function(id) {
      this.$Progress.start()
      axios
        .get(RecordURL + 'checkfiles?docID=' + id, {
          headers: authHeader()
        })
        .then(response => {
          if (response.data.withfiles) {
            this.download(id)
          } else {
            notifyHelper(
              'info',
              'Attached file not found or the file server was turned off!'
            )
          }
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    fetchData() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinkstodocument', {
          headers: authHeader(),
          params: this.pagingInfo
        })
        .then(response => {
          this.docs = response.data.data
          this.totalitemsdoc = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchData() {
      this.pagingInfo.page = 1
      this.fetchData()
    },
    more: function(data, type) {
      if (type === 'DC') {
        var additional = data.subject
        additional = additional.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional }
        })
      } else if (type === 'PN') {
        var additional1 =
          data.firstname + data.middlename + data.lastname + data.alias
        additional1 = additional1.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional1 }
        })
      } else if (type === 'OG') {
        var additional2 = data.name
        additional2 = additional2.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional2 }
        })
      } else if (type === 'EV') {
        var additional3 = data.subject
        additional3 = additional3.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional3 }
        })
      } else if (type === 'FR') {
        var additional4 = data.accountno
        additional4 = additional4.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional4 }
        })
      } else if (type === 'FA') {
        var additional5 = data.serialno
        additional5 = additional5.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional5 }
        })
      } else if (type === 'LC') {
        var additional6 = data.charge
        additional6 = additional6.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional6 }
        })
      } else if (type === 'RE') {
        var additional3 = data.type
        additional3 = additional3.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional3 }
        })
      } else if (type == 'TE') {
        var additional3 = data.phoneno
        additional3 = additional3.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional3 }
        })
      } else if (type == 'PR') {
        var additional3 = data.type
        additional3 = additional3.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional3 }
        })
      } else if (type == 'TR') {
        var additional3 = data.name
        additional3 = additional3.replace(/\//g, '-')
        this.$router.replace({
          name: 'Links',
          params: { id: data.id, description: additional3 }
        })
      }
    },
    sort: function(sortBy) {
      if (sortBy === this.pagingInfo.sortBy) {
        this.pagingInfo.reverse = !this.pagingInfo.reverse
      } else {
        this.pagingInfo.sortBy = sortBy
        this.pagingInfo.reverse = false
      }
      this.pagingInfo.page = 1
      this.fetchData()
    },
    searchPerson() {
      this.pagingInfoPerson.page = 1
      this.fetchPerson()
    },
    fetchPerson() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinkstoperson', {
          headers: authHeader(),
          params: this.pagingInfoPerson
        })
        .then(response => {
          this.persons = response.data.data
          this.totalitemsperson = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchOrganization() {
      this.pagingInfoOrganization.page = 1
      this.fetchOrganization()
    },
    fetchOrganization() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinksorganization', {
          headers: authHeader(),
