        })
        .finally(() => this.$Progress.finish())
    },
    sortev: function(sortBy) {
      if (sortBy === this.pagingInfoEvents.sortBy) {
        this.pagingInfoEvents.reverse = !this.pagingInfoEvents.reverse
      } else {
        this.pagingInfoEvents.sortBy = sortBy
        this.pagingInfoEvents.reverse = false
      }
      this.pagingInfoEvents.page = 1
      this.fetchEvents()
    },
    searchFinancial() {
