        })
        .then(response => {
          this.financial = response.data.data
          this.totalitemsfinancial = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchFirearms() {
      this.pagingInfoFirearms.page = 1
      this.fetchFireArms()
    },
    fetchFireArms() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinksfirearm', {
          headers: authHeader(),
