        })
        .then(response => {
          this.firearms = response.data.data
          this.totalitemsfirearm = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchLegalCharges() {
      this.pagingInfoLegalCharges.page = 1
      this.fetchLegalCharges()
    },
    fetchLegalCharges() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinkslegalcharges', {
