          params: this.pagingInfoLegalCharges
        })
        .then(response => {
          this.legalcharges = response.data.data
          this.totalitemslegalcharges = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchRealEstate() {
      this.pagingInfoRealEstates.page = 1
      this.fetchRealEstates()
    },
    fetchRealEstates() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinksrealestate', {
          headers: authHeader(),
          params: this.pagingInfoRealEstates
        })
        .then(response => {
          this.realestate = response.data.data
          this.totalitemsrealestate = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchTelephone() {
      this.pagingInfoTelephone.page = 1
      this.fetchTelephone()
    },
    fetchTelephone() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinkstelephone', {
          headers: authHeader(),
          params: this.pagingInfoTelephone
        })
        .then(response => {
          this.telephone = response.data.data
          this.totalitemstelephone = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchPapers() {
      this.pagingInfoPapers.page = 1
      this.fetchPapers()
    },
    fetchPapers() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinksofficialpapers', {
          headers: authHeader(),
          params: this.pagingInfoPapers
        })
        .then(response => {
          this.papers = response.data.data
          this.totalitemspaper = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    searchTransportation() {
      this.pagingInfoTransportation.page = 1
      this.fetchTransportation()
    },
    fetchTransportation() {
      this.$Progress.start()
      axios
        .get(RecordURL + 'getlinkstransportation', {
          headers: authHeader(),
          params: this.pagingInfoTransportation
        })
        .then(response => {
          this.transportation = response.data.data
          this.totalitemstransportation = response.data.count
        })
        .catch(error => {
          this.responsecheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    responsecheck: function(status) {
      responseCheck(status)
    },
    sois: function(id) {
      this.$Progress.start()
      axios
        .get(RecordURL + 'soi?pID=' + id, {
          headers: authHeader()
        })
        .then(response => {
          if (response.data.withsoi) {
            //modal
            this.soi = response.data.data
            //fireup modal
            this.showModal()
          } else {
            notifyHelper('warn', 'SOI files not found')
          }
        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    },
    downloadsoi: function(folder, file) {
      window.open(
        RecordURL + 'downloadsoi?folder=' + folder + '&filename=' + file,
        '_blank',
        ''
      )
    },
    showModal: function() {
      this.$bvModal.show('soilist')
    },
    soidb: function(id, type) {
      this.$Progress.start()
      axios
        .get(RecordURL + 'soidb?id=' + id + '&type=' + type, {
          headers: authHeader()
        })
        .then(response => {
          if (response.data.withsoi) {
            this.soifromdb = response.data.data
            this.$bvModal.show('soifromdb')
          } else {
            notifyHelper('warn', 'SOI from database not found')
          }
        })
        .catch(error => {
          responseCheck(error.response.status)
        })
        .finally(() => this.$Progress.finish())
    }
  }
}
</script>



