import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({

    state: {
        token : "",
        login : false,
        role : "",
        tfa : false,
        psk : "",
        haspsk: false,
        otpauthURI : ''
    },
    mutations: {
        addtoken(state,token){
            state.token = token
            state.login = true
        },
        addrole(state,role){
            state.role = role
        },
        addpsk(state, psk){
            state.psk = psk
        },
        haspsk(state, haspsk){
            state.haspsk = haspsk
        },
        addtfa(state,tfa){
            state.tfa = tfa
        },
        adduri(state,uri){
           state.otpauthURI = uri
        },
        removetoken(state)
        {
            state.token = ""
            state.role = ""
            state.login = false
            state.tfa = false
            state.psk = ""
            state.haspsk = false
        }
    },
    getters: {
        token : state => state.token,
        login : state => state.login,
        role : state => state.role,
        psk : state => state.psk,
        tfa : state => state.tfa,
        haspsk : state => state.haspsk,
        uri : state => state.otpauthURI
    }
})



