<template>
    <div>
            <div class="col-md-6 col-sm-6 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>Encoded By Region <small>Previous year</small></h2>
                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <br>
                        <ve-bar :data="chartDataLastYearEncoded"></ve-bar>
                    </div>
                </div>
            </div>
            <div class="col-md-6 col-sm-6 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>Encoded By Region <small>Current year</small></h2>
                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <br>
                        <ve-bar :data="chartDataLatestEncoded"></ve-bar>
                    </div>
                </div>
            </div>
            <div class="col-md-6 col-sm-6 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>Linked Entities By Region <small>Previous year</small></h2>
                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <br>
                        <ve-bar :data="chartDataLastYearLinked"></ve-bar>
                    </div>
                </div>
            </div>
            <div class="col-md-6 col-sm-6 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>Linked Entities By Region <small>Current year</small></h2>
                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <br>
                        <ve-bar :data="chartDataLatestLinked"></ve-bar>
                    </div>
                </div>
            </div>
    </div>
    
</template>

<script>
import axios from 'axios'
import {instance, authHeader,responseCheck,RecordURL } from '../helper';

export default {
    data () {
        return {
            chartDataLatestEncoded:{
                columns: ["region", "count"],
                rows :[]
            },
            chartDataLastYearEncoded:{
                columns: ["region", "count"],
                rows :[]
            },
            chartDataLatestLinked:{
                columns: ["region", "count"],
                rows :[]
            },
            chartDataLastYearLinked:{
                columns: ["region", "count"],
                rows :[]
            }
            
            
        }
    },
    mounted(){
        this.loadEncodedPrevious();
        this.loadEncodedCurrent();
        this.loadLinkedPrevious();
        this.loadLinkedCurrent();
    },
    methods:{
        loadEncodedCurrent(){
            this.$Progress.start()
            axios.get(RecordURL +'Current',
                {
                    headers : authHeader()
                   
                }
                )
                .then(response => {
                this.chartDataLatestEncoded.rows = response.data.data;
                this.$Progress.finish()
                })
                .catch(error => {
                    responseCheck(error.response.status)
                })
                .finally(() => this.$Progress.finish())
        },
        loadEncodedPrevious(){
                this.$Progress.start()
                axios.get( RecordURL +'Previous',
                {
                    headers : authHeader()
                }
                )
                .then(response => {
                this.chartDataLastYearEncoded.rows = response.data.data;
                this.$Progress.finish()
                })
                .catch(error => {
                    responseCheck(error.response.status)
                })
                .finally(() => this.$Progress.finish())
        },
        loadLinkedCurrent(){
             this.$Progress.start()
                axios.get(RecordURL +'LinkedCurrent',
                {
                    headers : authHeader()
                }
                )
                .then(response => {
                this.chartDataLatestLinked.rows = response.data.data;
                this.$Progress.finish()
                })
                .catch(error => {
                    responseCheck(error.response.status)
                })
                .finally(() => this.$Progress.finish())
        },
        loadLinkedPrevious(){
            this.$Progress.start()
                axios.get(RecordURL + 'LinkedPrevious',
                {
                    headers : authHeader()
                }
                )
                .then(response => {
                this.chartDataLastYearLinked.rows = response.data.data;
                this.$Progress.finish()
                })
                .catch(error => {
                    responseCheck(error.response.status)
                })
                .finally(() => this.$Progress.finish())
        }
    }
}
</script>


